import React from "react";
import Chart from 'chart.js/auto';
import { Bar } from "react-chartjs-2"
// import { faker } from '@faker-js/faker';

const options = {
  responsive: true,
  scales: {
    x: {
      grid: {
        display: false,
      },
      ticks: {
        display: true,
      }
    },
    y: {
      beginAtZero: true,
        display: false
    }
  },
  plugins:{	
    legend: {
      display: false,
    }
  }
};
const BarChart = ({days, data: value}) => {
     const data = {
  		labels: days,
  		datasets: [
		{
			data: value,
			backgroundColor: 'hsl(10, 79%, 65%)',
      borderRadius: 3,
      backgroundColor:"rgba(236, 119, 95)",
		}
  ],
};

    return(
            <Bar
            height={220}
            data={data}
            options={options}
            />
    )
}

export default BarChart;